.modal {
  position: fixed;
  z-index: 90;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
}

.modal._is-active {
  display: block;
}

.modal__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: .5;
  background-color: #000;
}

.modal__body {
  position: fixed;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  max-width: calc(100vw - 48px);
  min-height: 48px;
  padding: 24px;
  transform: translate(-50%, -50%);
  background-color: #fff;
}

.modal__close {
  font-size: 0;
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 24px;
  padding: 0;
  border: 0;
  background: none;
}

.modal__close::before,
.modal__close::after {
  position: absolute;
  top: calc(50% - 1px);
  right: 6px;
  left: 6px;
  height: 2px;
  content: "";
  background-color: #bbb;
}

.modal__close::before {
  transform: rotate(45deg);
}

.modal__close::after {
  transform: rotate(-45deg);
}